import { JSX, Show, createSignal } from "solid-js";

export default function Menu(props: { children: JSX.Element[] }) {
  const [show, setShow] = createSignal(false);
  let nav: any;

  return (
    <>
      <button
        onClick={() => setShow(true)}
        class="rounded-full bg-sky-800 text-white w-10 h-10"
      >
        ☰
      </button>
      <Show when={show()}>
        <nav
          class="bg-black bg-opacity-40 absolute inset-0 max-h-screen"
          onClick={(event) => event.target === nav && setShow(false)}
          ref={nav}
        >
          <button
            onClick={() => setShow(false)}
            class="w-10 h-10 absolute top-0 right-0"
          >
            ✕
          </button>
          <ul class="bg-white grid grid-cols-4 gap-4  xl:m-96 p-8 xl:rounded-2xl">
            {props.children}
          </ul>
        </nav>
      </Show>
    </>
  );
}
