// @refresh reload
import { Show, Suspense } from "solid-js";
import {
  useLocation,
  A,
  Body,
  ErrorBoundary,
  FileRoutes,
  Head,
  Html,
  Meta,
  Routes,
  Scripts,
  Title,
} from "solid-start";
import { Router, hashIntegration } from "@solidjs/router";
import "./root.css";
import "animate.css";

import Menu from "./components/Menu";
import "./lib/sentry";
import { docHeight } from "./lib/docHeight";

export default function Root() {
  const location = useLocation();
  const active = (path: string) =>
    path == location.pathname
      ? "border-sky-600"
      : "border-transparent hover:border-sky-600";

  const inIframe = () => {
    try {
      return window.self !== window.top;
    } catch (e) {
      return true;
    }
  };

  docHeight();

  return (
    <Html lang="en" class="overscroll-none">
      <Head>
        <Title>SolidStart - With TailwindCSS</Title>
        <Meta charset="utf-8" />
        <Meta name="viewport" content="width=device-width, initial-scale=1" />
      </Head>
      <Body>
        <Suspense>
          <ErrorBoundary>
            <Show when={false}>
              <Menu>
                <li class={`${active("/")} hover:shadow-lg aspect-square`}>
                  <A
                    href="/"
                    class="group justify-center items-center flex flex-col w-full h-full text-[8vw]"
                  >
                    🆔
                    <span class="group-hover:visible invisible text-base">
                      Norwegian ID Generator
                    </span>
                  </A>
                </li>
                <li
                  class={`${active("/about")} hover:shadow-lg aspect-square `}
                >
                  <A
                    href="/about"
                    class="justify-center items-center flex w-full h-full "
                  >
                    About
                  </A>
                </li>
              </Menu>
            </Show>

            <Routes>
              <FileRoutes />
            </Routes>
          </ErrorBoundary>
        </Suspense>
        <Scripts />
      </Body>
    </Html>
  );
}
